<template>
  <div class="rightManage">
    <el-button type="success" @click="register" class="register"
      >注册</el-button
    >
    <div class="table">
      <table>
        <h2>人员权限分配</h2>
        <thead>
          <tr>
            <th>序号</th>
            <th>姓名</th>
            <th>账号</th>
            <th>部门</th>
            <th>站点分配</th>
            <th>账号类型</th>
            <th>账号状态</th>
            <th style="width: 20vw">操作</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in rightsData" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ item.real_name }}</td>
            <td>{{ item.account }}</td>
            <td>{{ item.department }}</td>
            <td>{{ item.area }}</td>
            <td>{{ item.roles }}</td>
            <td v-if="item.state == 1">启用</td>
            <td v-else>停用</td>
            <td style="width: 20vw">
              <div style='display"flex'>
                <el-button
                  type="warning"
                  size="mini"
                  @click="editRights(item.real_name, item.account)"
                  >编辑</el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  @click="delRights(item.account)"
                  >删除</el-button
                >
                <el-button
                  type="info"
                  size="mini"
                  @click="delPassword(item.account)"
                  >修改密码</el-button
                >
                <el-button
                  type="success"
                  size="mini"
                  @click="station_sign(item.account)"
                  >站点标记</el-button
                >
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="table">
      <table>
        <h2>账号类型权限分配</h2>
        <thead>
          <tr>
            <th>账号类型</th>
            <th v-for="(item, index) in accountTypeAllo" :key="index">
              {{ item.role_name }}
            </th>
            <!-- <th v-for="(role) in item" :key="role">{{ role.role_name }}</th> -->
            <!-- <th>账号类型</th>
             <th>总监</th>
            <th>主管</th>
            <th>专员A</th>
            <th>专员B</th>
            <th>专员c</th>-->
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>账号权限</td>
            <td v-for="(item, index) in accountTypeAllo" :key="index">
              <p v-for="permissions in item.title" :key="permissions">
                {{ permissions }}
              </p>
              <!-- <p v-for="(item,index) in accountTypeAllo[0]['title']" :key="index">{{item}}</p>
            </td>
            <td>
              <p v-for="(item,index) in accountTypeAllo[1]['title']" :key="index">{{item}}</p>
            </td>
            <td>
              <p v-for="(item,index) in accountTypeAllo[2]['title']" :key="index">{{item}}</p>
              </td>-->
              <!-- <td> -->
              <!-- <p v-for="(item,index) in accountTypeAllo['专员B']" :key="index">{{item}}</p> -->
            </td>
          </tr>
        </tbody>
      </table>
      <div style="margin-top: 10px">
        <el-button type="warning" size="mini" @click="editAccountRights"
          >编辑</el-button
        >
        <el-button type="primary" size="mini" @click="addAccountRights"
          >新增</el-button
        >
      </div>
    </div>

    <!-- 编辑员工权限 -->
    <el-dialog
      title="权限分配"
      :visible.sync="editRightsDialog.editRightsVisible"
      width="30%"
      center
      @close="editRightsDialogClose"
    >
      <!-- 表单区域 -->
      <el-form
        ref="editRightsRef"
        :model="editRightsDialog.editRightsForm"
        label-width="80px"
        :rules="formRoules"
      >
        <el-form-item label="姓名" prop="checkedName">
          <el-input
            v-model="editRightsDialog.editRightsForm.checkedName"
          ></el-input>
        </el-form-item>
        <el-form-item label="账号类型" prop="checkedType">
          <el-select
            v-model="editRightsDialog.editRightsForm.checkedType"
            placeholder="请选择"
          >
            <el-option
              v-for="item in accountTypeAllo"
              :key="item.role_name"
              :label="item.role_name"
              :value="item.role_name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="账号状态" prop="checkedState">
          <el-select
            v-model="editRightsDialog.editRightsForm.checkedState"
            placeholder="请选择"
          >
            <el-option
              v-for="item in editRightsDialog.accountState"
              :key="item"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editRightsDialog.editRightsVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="save_edit()">保 存</el-button>
      </span>
    </el-dialog>

    <!-- 编辑账号类型权限 -->
    <el-dialog
      title="编辑账号类型权限"
      :visible.sync="editAccountDialog.editAccountVisible"
      width="30%"
      center
      @close="editAccountDialogClose"
    >
      <el-form
        ref="editAccountRef"
        :model="editAccountDialog.editAccountForm"
        label-width="80px"
        :rules="formRoules"
      >
        <!-- 表单内容 -->
        <el-form-item label="账号类型" prop="checkedType">
          <el-select
            v-model="editAccountDialog.editAccountForm.checkedType"
            placeholder="请选择"
            @change="accountTypeChange"
          >
            <el-option
              v-for="item in accountTypeAllo"
              :key="item.role_name"
              :label="item.role_name"
              :value="item.role_name"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="权限更改">
          <el-checkbox-group
            v-model="editAccountDialog.editAccountForm.checkedRights"
          >
            <el-checkbox
              v-for="item in allPermisPage"
              :key="item.id"
              :label="item.title"
            ></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editAccountDialog.editAccountVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="editAccountType">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 新增权限 -->
    <el-dialog
      title="新增权限分配"
      :visible.sync="addAccountDialog.addAccountVisible"
      width="30%"
      center
      @close="addAccountDialogClose"
    >
      <el-form
        ref="addAccountRef"
        :model="addAccountDialog.addAccountForm"
        label-width="80px"
        :rules="formRoules"
      >
        <!-- 表单内容 -->
        <el-form-item label="账号类型" prop="accountType">
          <el-input
            v-model="addAccountDialog.addAccountForm.addAcountName"
          ></el-input>
        </el-form-item>
        <el-form-item label="权限选择">
          <el-checkbox-group
            v-model="addAccountDialog.addAccountForm.checkedRights"
          >
            <el-checkbox
              v-for="item in allPermisPage"
              :key="item.id"
              :label="item.title"
            ></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addAccountDialog.addAccountVisible = false"
          >取 消</el-button
        >
        <el-button
          type="primary"
          @click="addAccountDialog.addAccountVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 注册 -->
    <el-dialog
      title="注册"
      :visible.sync="registerDialog.registerVisible"
      width="30%"
      center
      @close="registerDialogClose"
    >
      <!-- 表单区域 -->
      <el-form
        ref="registerRef"
        :model="registerDialog.registerForm"
        label-width="80px"
        :rules="formRoules1"
      >
        <el-form-item label="姓名" prop="registerName">
          <el-input
            v-model="registerDialog.registerForm.registerName"
          ></el-input>
        </el-form-item>
        <el-form-item label="电话" prop="registerPhone">
          <el-input
            v-model="registerDialog.registerForm.registerPhone"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="registerPassword">
          <el-input
            v-model="registerDialog.registerForm.registerPassword"
          ></el-input>
        </el-form-item>
        <el-form-item label="账号类型" prop="registerType">
          <el-select
            v-model="registerDialog.registerForm.registerType"
            placeholder="请选择"
          >
            <el-option
              v-for="(item, index) in registerDialog.registerType"
              :key="index"
              :label="item.roles"
              :value="item.roles"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="registerDialog.registerVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="register_sure()">注 册</el-button>
      </span>
    </el-dialog>

    <!-- 修改密码 -->
    <el-dialog
      title="修改密码"
      :visible.sync="passwordDialog.passwordVisible"
      width="30%"
      center
      @close="passwordDialogClose"
    >
      <!-- 表单区域 -->
      <el-form
        ref="passwordRef"
        :model="passwordDialog.passwordForm"
        label-width="80px"
      >
        <el-form-item label="新密码" prop="password">
          <el-input v-model="passwordDialog.passwordForm.password"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="passwordDialog.passwordVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="password_sure()">确定</el-button>
      </span>
    </el-dialog>
    <!-- 站点标记 -->
    <el-dialog
      title="站点标记"
      :visible.sync="stationDialog.stationVisible"
      width="30%"
      center
      @close="stationDialogClose"
    >
      <!-- 表单区域 -->
      <el-form
        ref="stationRef"
        :model="stationDialog.stationForm"
        label-width="80px"
      >
        <el-select
          v-model="station"
          multiple
          collapse-tags
          style="margin-left: 20px"
          placeholder="请选择"
        >
          <el-option
            v-for="item in stationDialog.stationForm.stationVal"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >
          </el-option>
        </el-select>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="stationDialog.stationVisible = false"
          >取 消</el-button
        >
        <el-button type="primary" @click="station_sure()">确定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "",
  props: {},
  data() {
    // let checkPhone = (rule, value, callback) => {
    //   const regPhone = /^[1][3,4,5,7,8][0-9]{9}$/;
    //   if (regPhone.test(value)) {
    //     return callback();
    //   } else {
    //     callback("请输入正确的手机号码");
    //   }
    // };
    return {
      // 人员权限分配列表  无接口自己定
      rightsData: [
        {
          num: 1,
          name: "黄分院",
          accountNum: "AAS",
          department: "物流",
          accountType: "总监",
          accountState: "启用"
        }
      ],
      // 所有的权限页面
      allPermisPage: ["权限页面1", "权限页面2", "权限页面3", "权限页面4"],
      // 账号类型权限分配 无接口自己配
      accountTypeAllo: {
        总监: ["权限页面1", "权限页面1", "权限页面1", "权限页面1"],
        主管: ["权限页面1", "权限页面1", "权限页面1"],
        专员A: ["权限页面1", "权限页面1"],
        专员B: ["权限页面1"]
      },
      roledata: {},
      // 编辑员工权限对话框相关数据
      editRightsDialog: {
        // 是否显示对话框
        editRightsVisible: false,
        // 表单内容信息
        editRightsForm: {
          // 获取选中的账号状态
          checkedState: "",
          // 获取当前员工姓名
          checkedName: "",
          // 获取选中的账号类型
          checkedType: "",
          checkedName1: ""
        },
        // 下拉框遍历账号类型
        accountType: ["总监", "主管", "专员A", "专员B"],
        // 下拉框遍历 账号状态
        accountState: ["启用", "停用"]
      },
      // 编辑账号权限对话框相关数据
      editAccountDialog: {
        editAccountVisible: false,
        editAccountForm: {
          // 获取当前选中的账号类型 （遍历账号类型可使用编辑员工权限那里的accountType）
          checkedType: "",
          // 权限更改选中的复选框组合
          checkedRights: []
        }
      },
      // 新增账号类型并分配权限对话框相关数据
      addAccountDialog: {
        addAccountVisible: false,
        addAccountForm: {
          // 添加的账号类型名称
          addAcountName: "",
          // 权限选择的复选框组合
          checkedRights: []
        }
      },

      //注册
      registerDialog: {
        // 是否显示对话框
        registerVisible: false,
        // 表单内容信息
        registerForm: {
          // 电话
          registerPhone: "",
          // 获取当前员工姓名
          registerName: "",
          // 密码
          registerPassword: "",
          //选中的类型
          registerType: ""
        },
        // 下拉框遍历账号类型
        registerType: ["总监", "主管", "专员A", "专员B"]
      },

      // 表单验证
      formRoules: {
        checkedName: [
          { required: true, message: "请输入姓名", trigger: "blur" }
        ],
        checkedType: [
          { required: true, message: "请选择账号类型", trigger: "blur" }
        ],
        checkedState: [
          { required: true, message: "请选择账号状态", trigger: "blur" }
        ]
      },

      //注册表单验证
      formRoules1: {
        registerName: [
          { required: true, message: "请输入姓名", trigger: "blur" }
        ],
        registerPhone: [
          { required: true, message: "请输入电话", trigger: "blur" }
          //   { validator: checkPhone, trigger: "blur" },
        ],
        registerPassword: [
          { required: true, message: "请输入密码", trigger: "blur" }
        ],
        registerType: [
          { required: true, message: "请选择账号状态", trigger: "blur" }
        ]
      },

      //修改密码
      passwordDialog: {
        // 是否显示对话框
        passwordVisible: false,
        // 表单内容信息
        passwordForm: {
          // 密码
          passwordPhone: ""
        }
      },
      password_info: "",
      //站点标记
      stationDialog: {
        // 是否显示对话框
        stationVisible: false,
        // 表单内容信息
        stationForm: {
          // 密码
          stationVal: []
        }
      },
      station: ["黄金糕"],
      station_account: ""
    };
  },
  computed: {},
  watch: {},

  created() {
    this.$axios({
      url: this._url2 + "server/stock_management/is_open/",
      method: "get"
    }).then(res => {
      if (res.data.code == 200) {
        this.GetUserList();
      } else {
        alert("您没有权限进入这个页面");
        this.$router.push("/Login");
      }
    });
  },
  methods: {
    GetUserList() {
      console.log(1231, "进来发请求");
      let that = this;

      this.$axios({
        url: that._url2 + "server/personnel_management/authority_management/",
        methods: "get"
      }).then(function(res) {
        console.log(233, res.data.code);
        if (res.data.code === 200) {
          console.log("这是全部数据", res.data);
          that.rightsData = res.data.user_data;
          that.rightsData.map((item, index) => {
            if (item.area == "all") {
              item.area = "全部";
            }
          });
          console.log("这是角色数据role_data", res.data.role_data);
          console.log("这是用户的数据", res.data.user_data);
          console.log("这是权限数据", res.data.permission_data);
          that.accountTypeAllo = res.data.role_data;
          that.allPermisPage = res.data.permission_data;
        } else {
          alert(res.data);
        }
      });
    },
    //确认编辑
    save_edit() {
      let that = this;
      let name = this.editRightsDialog.editRightsForm.checkedName;
      let type = this.editRightsDialog.editRightsForm.checkedType;
      let state = this.editRightsDialog.editRightsForm.checkedState;
      let text_name = this.editRightsDialog.editRightsForm.checkedName1;
      if (state == "启用") {
        state = "1";
      } else {
        state = "0";
      }
      let formData = new FormData();
      formData.append("account", text_name);
      formData.append("state", state);
      formData.append("roles", type);
      formData.append("real_name", name);
      this.$axios
        .post(
          that._url2 + "server/personnel_management/authority_management/",
          formData
        )
        .then(function(res) {
          console.log(233, res.data);
          if (res.data.code === 200) {
            console.log(res.data);
            that.rightsData = res.data.user_data;
            that.editRightsDialog.editRightsVisible = false;
          } else {
            alert(res.data);
          }
        });
    },
    // 点击编辑员工权限
    editRights(name, name1) {
      this.editRightsDialog.editRightsForm.checkedName = name;
      this.editRightsDialog.editRightsForm.checkedName1 = name1;
      this.editRightsDialog.editRightsVisible = true;
    },
    // 点击编辑账号权限
    editAccountRights() {
      this.editAccountDialog.editAccountVisible = true;
    },
    // 点击新增账号
    addAccountRights() {
      this.addAccountDialog.addAccountVisible = true;
    },
    // 编辑账号类型权限中账号类型被选中时触发
    accountTypeChange(val) {
      this.editAccountDialog.editAccountForm.checkedRights = [];
      console.log(val);
      this.accountTypeAllo.forEach(n => {
        if (n.role_name == val) {
          this.editAccountDialog.editAccountForm.checkedRights.push(...n.title);
        }
      });
    },
    // 对话框关闭事件重置表单
    editRightsDialogClose() {
      this.$refs.editRightsRef.resetFields();
      console.log();
    },
    editAccountDialogClose() {
      this.$refs.editAccountRef.resetFields();
    },
    addAccountDialogClose() {
      this.$refs.addAccountRef.resetFields();
    },
    registerDialogClose() {
      this.$refs.registerRef.resetFields();
    },
    passwordDialogClose() {
      this.$refs.passwordRef.resetFields();
    },
    stationDialogClose() {
      this.$refs.stationRef.resetFields();
    },

    // 点击确认编辑账号权限
    editAccountType() {
      let type = this.editAccountDialog.editAccountForm.checkedType;
      let rights = [];
      for (let item of this.editAccountDialog.editAccountForm.checkedRights) {
        for (let i of this.allPermisPage) {
          if (i.title == item) {
            rights.push(i.id);
          }
        }
      }
      let formData = new FormData();
      formData.append(" permission_id_list", rights);
      formData.append("role_name", type);
      this.$axios
        .post(
          that._url2 + "server/personnel_management/authority_management/",
          formData
        )
        .then(function(res) {
          console.log(233, res.data);
          if (res.data.code === 200) {
            console.log(res.data);
            that.rightsData = res.data.user_data;
            that.editRightsDialog.editRightsVisible = false;
          } else {
            alert(res.data);
          }
        });
    },

    //注册弹框
    register() {
      let that = this;
      this.registerDialog.registerVisible = true;
      this.$axios({
        url: that._url2 + "server/personnel_management/get_all_rolas/",
        methods: "get"
      }).then(function(res) {
        if (res.data.code === "200") {
          console.log(res.data);
          that.registerDialog.registerType = res.data.data;
        } else {
          alert(res.data);
        }
      });
    },

    //确定注册
    register_sure() {
      let that = this;
      this.$refs.registerRef.validate(val => {
        if (!val) return false;

        let formData = new FormData();
        formData.append("name", this.registerDialog.registerForm.registerName);
        formData.append("tel", this.registerDialog.registerForm.registerPhone);
        formData.append(
          "password",
          this.registerDialog.registerForm.registerPassword
        );
        formData.append("roles", this.registerDialog.registerForm.registerType);
        this.$axios
          .post(that._url2 + "server/personnel_management/add_user/", formData)
          .then(function(res) {
            if (res.data.code === "200") {
              that.$message.success("注册成功");
              that.registerDialog.registerVisible = false;
              window.location.reload();
            } else {
              that.$message.error(res.data.msg);
            }
          });
      });
    },

    //删除人员
    delRights(account) {
      let that = this;
      console.log(account);
      let formData = new FormData();
      formData.append("account", account);
      this.$axios
        .post(that._url2 + "server/personnel_management/delete_user/", formData)
        .then(function(res) {
          if (res.data.code === 200) {
            alert("删除成功");
            window.location.reload();
          } else {
            that.$message.error("删除失败");
          }
        });
    },

    //修改密码弹框
    delPassword(info) {
      this.passwordDialog.passwordVisible = true;
      this.password_info = info;
    },
    //修改密码确认
    password_sure() {
      let that = this;
      let account = this.password_info;
      let password = this.passwordDialog.passwordForm.password;
      let formData = new FormData();
      formData.append("account", account);
      formData.append("password", password);
      this.$axios
        .post(that._url2 + "server/personnel_management/update_pswd/", formData)
        .then(function(res) {
          if (res.data.code === 200) {
            that.$message.success("修改成功");
            // window.location.reload();
          } else {
            that.$message.error("修改失败");
          }
        });
    },
    //修改站点标记弹框
    station_sign(account) {
      this.stationDialog.stationVisible = true;
      this.station_account = account;
      let that = this;
      this.$axios
        .get(that._url2 + "server/personnel_management/area_sign_data/", {
          params: {
            account: account
          }
        })
        .then(function(res) {
          if (res.data.code === 200) {
            console.log(res.data);
            that.stationDialog.stationForm.stationVal = res.data.data1;
            that.station = res.data.data;
          } else {
          }
        });
    },
    //修改站点确认
    station_sure() {
      let that = this;
      let account = this.station_account;
      let area = this.station;
      console.log(account, area);
      let formData = new FormData();
      formData.append("account", account);
      formData.append("area", area);
      this.$axios
        .post(that._url2 + "server/personnel_management/area_sign/", formData)
        .then(function(res) {
          if (res.data.code === 200) {
            // that.$message.success("修改成功");
            alert(res.data.msg);
            window.location.reload();
          } else {
            that.$message.error("修改站点分配失败");
          }
        });
    }
  }
};
</script>
<style scoped>
@import "../../../static/css/thead_fixed_table.css";
body {
  overflow-x: hidden;
}
.table {
  margin-top: 60px;
}
.el-checkbox-group {
  display: flex;
  flex-direction: column;
}
.el-checkbox {
  margin-left: 14px;
}
p {
  width: 100%;
}
.register {
  width: 150px;
  height: 35px;
  line-height: 35px;
  text-align: center;
}
.success {
  position: absolute;
  top: 250px;
  left: 20px;
}
.rightManage {
  text-align: center;
}
.register {
  position: relative;
  top: 5vh;
  left: -31vw;
  height: 40px;
  line-height: 16px;
}
</style>
<style>
.el-dialog .el-form > * {
  margin-bottom: 22px;
}
</style>



